// export default axios;
// 10s
axios.defaults.timeout = 10000;


export default {
    //get
    get(url, param) {
        return axios({
            method: 'get',
            url,
            params: param,
            cancelToken: new CancelToken(c => {
                cancel = c
            })
        })
    },
    //post
    post(url, param) {
        return axios({
            method: 'post',
            url,
            data: param,
            cancelToken: new CancelToken(c => {
                cancel = c
            })
        })
    }
}


